import Vue from 'vue'
import App from './App.vue'
import router from './router'
Vue.directive('rainbow', {
  bind(el) {
    el.style.color = `#${Math.random().toString(16).slice(2, 8)}`
  }
})
Vue.config.productionTip = false
Vue.filter('snippet', value => value.slice(0, 180) + '...')

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
